import EventTarget from 'scanex-event-target';
import './Slider.css';

class Slider extends EventTarget {
    constructor (container, options = {}) {
        super();
        this._options = options;
        this._options.orientation = this._options.orientation || 'horizontal';
        this._container = container;
        this._container.classList.add('slider');
        this._container.classList.add('noselect');
        this._container.innerHTML = 
            `<div class="slider-bar">
                <div class="slider-tick">
                    <div class="slider-tip">00</div>
                </div>
            </div>`;
        this._bar = this._container.querySelector('.slider-bar');
        this._tick = this._container.querySelector('.slider-tick');    
        this._tick.classList.add('noselect');
        this._tip = this._container.querySelector('.slider-tip');
        const tickRect = this._tick.getBoundingClientRect();
        this._halfWidth = Math.round(tickRect.width / 2);
        this._halfHeight = Math.round(tickRect.height / 2);
        this._moving = false;        
        switch (this._options.orientation) {
            default:
            case 'horizontal':
                this._tick.style.left = `${-this._halfWidth}px`;
                this._startHorizontal = this._startHorizontal.bind(this);
                this._moveHorizontal = this._moveHorizontal.bind(this);
                this._clickHorizontal = this._clickHorizontal.bind(this);
                this._tip.style.top = '-20px';
                this._bar.addEventListener ('click', this._clickHorizontal);
                this._tick.addEventListener ('mousedown', this._startHorizontal);
                document.addEventListener ('mousemove', this._moveHorizontal);         
                break;
            case 'vertical':
                this._tick.style.top = `${-this._halfHeight}px`;
                this._startVertical = this._startVertical.bind(this);
                this._moveVertical = this._moveVertical.bind(this);
                this._clickVertical = this._clickVertical.bind(this);
                this._tip.style.top = '-5px';
                this._tip.style.left = '20px';
                this._bar.addEventListener ('click', this._clickVertical);
                this._tick.addEventListener ('mousedown', this._startVertical);
                document.addEventListener ('mousemove', this._moveVertical);
                break;
        }        
        this._stop = this._stop.bind(this);
        document.addEventListener ('mouseup', this._stop);

        const containerRect = this._container.getBoundingClientRect();
        this._width = containerRect.width;
        this._height = containerRect.height;
        this._offset = 0;
    }            

    _clickHorizontal (e) {
        e.stopPropagation();
        const containerRect = this._container.getBoundingClientRect();
        let x = e.clientX - this._halfWidth - containerRect.left;
        if (x <= -this._halfWidth) {
            this._tick.style.left = `${-this._halfWidth}px`;                        
            x = -this._halfWidth;
        }
        else if (x >= this._width - this._halfWidth) {
            this._tick.style.left = `${this._width - this._halfWidth}px`;
            x = this._width - this._halfWidth;
        }
        else {
            this._tick.style.left = `${x}px`;
        }        

        let event = document.createEvent('Event');
        event.initEvent('slide', false, false);
        event.detail = {value: x + this._halfWidth };
        this.dispatchEvent(event);
    }

    _startHorizontal (e) {
        e.stopPropagation();
        const tickRect = this._tick.getBoundingClientRect();
        this._offset = e.clientX - tickRect.left;
        this._moving = true;
        if (this._options.useTip) {
            this._tip.style.display = 'block';
        }
    }

    _moveHorizontal (e) {
        e.stopPropagation();
        if (this._moving) {            
            document.body.style.cursor = 'pointer';
            const containerRect = this._container.getBoundingClientRect();
            let x = e.clientX - this._offset - containerRect.left;
            if (x <= -this._halfWidth) {
                this._tick.style.left = `${-this._halfWidth}px`;
                x = -this._halfWidth;
            }
            else if (x >= this._width - this._halfWidth) {
                this._tick.style.left = `${this._width - this._halfWidth}px`;
                x = this._width - this._halfWidth;
            }
            else {
                this._tick.style.left = `${x}px`;
            }                
            if (this._options.useTip) {
                this._tip.style.display = 'block';
            }

            let event = document.createEvent('Event');
            event.initEvent('slide', false, false);
            event.detail = {value: x + this._halfWidth, tip: this._tip};
            this.dispatchEvent(event);
        }
    }

    _clickVertical (e) {
        e.stopPropagation();
        const containerRect = this._container.getBoundingClientRect();
        let y = e.clientY - this._halfHeight - containerRect.top;
        if (y <= -this._halfHeight) {
            this._tick.style.top = `${-this._halfHeight}px`;
            y = -this._halfHeight;
        }
        else if (y >= this._height - this._halfHeight) {
            this._tick.style.top = `${this._height - this._halfHeight}px`;
            y = this._height - this._halfHeight;
        }
        else {
            this._tick.style.top = `${y}px`;
        }        
    
        let event = document.createEvent('Event');
        event.initEvent('slide', false, false);
        event.detail = {value: y + this._halfHeight};
        this.dispatchEvent(event);
    }

    _startVertical (e) {
        e.stopPropagation();
        const tickRect = this._tick.getBoundingClientRect();
        this._offset = e.clientY - tickRect.top;
        this._moving = true;
    }

    _moveVertical (e) {
        e.stopPropagation();
        if (this._moving) {
            document.body.style.cursor = 'pointer';
            const containerRect = this._container.getBoundingClientRect();
            let y = e.clientY - this._offset - containerRect.top;
            if (y <= -this._halfHeight) {
                this._tick.style.top = `${-this._halfHeight}px`;
                y = -this._halfHeight;
            }
            else if (y >= this._height - this._halfHeight) {
                this._tick.style.top = `${this._height - this._halfHeight}px`;                        
                y = this._height - this._halfHeight;
            }
            else {
                this._tick.style.top = `${y}px`;
            }
            if (this._options.useTip) {
                this._tip.style.display = 'block';
            }        

            let event = document.createEvent('Event');
            event.initEvent('slide', false, false);
            event.detail = {value: y + this._halfHeight, tip: this._tip};
            this.dispatchEvent(event);
        }
    }

    _stop (e) {
        e.stopPropagation();
        this._moving = false;
        this._offset = 0;
        document.body.style.cursor = 'default';
        if (this._options.useTip) {
            this._tip.style.display = 'none';
        }
    }

    get value () {
        const tickRect = this._tick.getBoundingClientRect();
        const containerRect = this._container.getBoundingClientRect();
        switch (this._options.orientation) {
            default:
            case 'horizontal':
                return tickRect.left + this._halfWidth - containerRect.left;
            case 'vertical':
                return tickRect.top + this._halfHeight - containerRect.top;
        }
    }

    set value (value) {
        const containerRect = this._container.getBoundingClientRect();
        switch (this._options.orientation) {
            default:
            case 'horizontal':
                this._tick.style.left = `${value - this._halfWidth}px`;
                break;
            case 'vertical':
                this._tick.style.top = `${value - this._halfHeight}px`;
                break;
        }
    }
}
    
export default Slider;
