const expand = (n, a, radix) => {
    if (n === 0) {
        return a;
    }
    else {
        return expand (Math.round(n / radix), a.concat(n % radix), radix);
    }
};

const stringToArray = str => {
    let arr = [];
    for (let i = 0; i < str.length; ++i) {
        arr.push (str.charAt(i));
    }
    return arr;
};

const pad = (origin, str, width, sym) => {
    let s = stringToArray (str);
    for (let i = 0; s.length < width; ++i) {
        if (origin === 'left') {
            s.splice (0, 0, sym);
        }
        else {
            s.push (sym);
        }
    }
    return s.join('');
};

const padLeft = (str, sym, width) => pad ('left', str, width, sym);
const padRight = (str, sym, width) => pad ('right', str, width, sym);

export {expand, stringToArray, pad, padLeft, padRight};