import Slider from '../Slider/Slider.js';
import './AlphaSlider.css';

class AlphaSlider extends Slider {
    constructor (container, options = {}){
        super(container, options);
        this._options.order = this._options.order || 'asc';
        this._options.rgb = this._options.rgb || {r: 0, g: 0, b: 0};
        this._container.classList.add ('alpha-slider');
        this._canvas = document.createElement('canvas');
        this._container.querySelector('.slider-bar').appendChild(this._canvas);         
        this._handleChange = this._handleChange.bind(this);
        this.addEventListener('slide', this._handleChange);
        this.setRGB(this._options.rgb);
    }        
    _handleChange (e) {        
        const a = e.detail.value / (this._options.orientation === 'horizontal' ? this._width : this._height);
        const alpha = this._options.order === 'desc' ? 1.0 - a : a;
        if (e.detail.tip) {
            e.detail.tip.innerText = Math.round (alpha * 100);
        }
        let event = document.createEvent('Event');
        event.initEvent('change', false, false);
        event.detail = {value: alpha};
        this.dispatchEvent(event);
    }
    // r (0,255), g (0,255), b (0,255)
    _renderHorizontal (r, g, b) {
        let ctx = this._canvas.getContext('2d');
        let imgData = ctx.getImageData (0, 0, this._canvas.width, this._canvas.height);
        let {data, width} = imgData;
        for (let i = 0; i < data.length; i += 4){
            var a = ((i / 4) % width) / width;
            data[i + 0] = r;
            data[i + 1] = g;
            data[i + 2] = b;
            data[i + 3] = Math.round (255 * (this._options.order === 'desc' ? 1.0 - a : a));
        }
        ctx.putImageData (imgData, 0, 0);        
    }
    // r (0,255), g (0,255), b (0,255)
    _renderVertical (r, g, b) {
        let ctx = this._canvas.getContext('2d');
        let imgData = ctx.getImageData (0, 0, this._canvas.width, this._canvas.height);
        let {data, width, height} = imgData;    
        for (let i = 0, j = 0, k = 0; i < data.length; i += 4, ++j){            
            if (j >= width) {
                ++k;
                j = 0;
            }
            let a = k / height;
            a = this._options.order === 'desc' ? 1.0 - a : a;
            data[i + 0] = r;
            data[i + 1] = g;
            data[i + 2] = b;
            data[i + 3] = Math.round (255 * a);
        }
        ctx.putImageData (imgData, 0, 0);        
    }
    setRGB (rgb) {
        this._options.rgb = rgb;
        const {r, g, b} = this._options.rgb;
        switch (this._options.orientation) {
            case 'horizontal':
                this._renderHorizontal (r, g, b);
                break;
            case 'vertical':
                this._renderVertical (r, g, b);
                break;
        }
    }
}    

export default AlphaSlider;