import EventTarget from 'scanex-event-target';
import { hsl2rgb } from '../Color.js';
import './ColorArea.css';

class ColorArea extends EventTarget {
    constructor (container, options) {
        super();
        this._options = options || {};
        this._options.hue = this._options.hue || 0;
        this._options.alpha = this._options.alpha || 1.0;
        this._container = container;
        this._container.classList.add('color-area');
        this._container.innerHTML = `<div class="picker"></div><canvas></canvas>`;

        this._canvas = this._container.querySelector ('.color-area canvas');
        this._click = this._click.bind(this);
        this._canvas.addEventListener ('click', this._click);
        this._picker = this._container.querySelector ('.color-area .picker');
        this._picker.style.top = '50%';
        this._picker.style.left = '50%';
        const pickerRect = this._picker.getBoundingClientRect();
        this._halfWidth = Math.round(pickerRect.width / 2);
        this._halfHeight = Math.round(pickerRect.height / 2);

        this._start = this._start.bind(this);
        this._move = this._move.bind(this);
        this._stop = this._stop.bind(this);
        this._moving = false;
        this._offsetX = 0;
        this._offsetY = 0;

        this._picker.addEventListener('mousedown', this._start);
        document.addEventListener('mousemove', this._move);
        document.addEventListener('mouseup', this._stop); 
                    
        this.setHue(this._options.hue);
    }    

    _click (e) {
        e.stopPropagation();
        const containerRect = this._container.getBoundingClientRect();
        // handle x
        let x = e.clientX - this._halfWidth - containerRect.left;
        this._picker.style.left = `${x}px`;

        // handle y
        let y = e.clientY - this._halfHeight - containerRect.top;
        this._picker.style.top = `${y}px`;
    
        let s = 1 - (x + this._halfWidth) / containerRect.width;
        let l = 1 - (y + this._halfHeight) / containerRect.height;
        
        let event = document.createEvent('Event');
        event.initEvent('change', false, false);
        event.detail = {h: this._options.hue, s, l};
        this.dispatchEvent(event);
    }
    _start (e) {
        e.stopPropagation();
        const pickerRect = this._picker.getBoundingClientRect();
        this._offsetX = e.clientX - pickerRect.left;        
        this._offsetY = e.clientY - pickerRect.top;
        this._moving = true;
    }
    _move (e) {
        e.stopPropagation();
        if (this._moving) {
            document.body.style.cursor = 'pointer';
            const containerRect = this._container.getBoundingClientRect();
            // handle x
            let x = e.clientX - this._offsetX - containerRect.left;
            if (x <= -this._halfWidth) {
                this._picker.style.left = `${-this._halfWidth}px`;                        
                x = -this._halfWidth;
            }
            else if (x >= containerRect.width - this._halfWidth) {
                this._picker.style.left = `${containerRect.width - this._halfWidth}px`;                        
                x = containerRect.width - this._halfWidth;
            }
            else {
                this._picker.style.left = `${x}px`;                        
            }
            // handle y
            let y = e.clientY - this._offsetY - containerRect.top;
            if (y <= -this._halfHeight) {
                this._picker.style.top = `${-this._halfHeight}px`;                        
                y = -this._halfHeight;
            }
            else if (y >= containerRect.height - this._halfHeight) {
                this._picker.style.top = `${containerRect.height - this._halfHeight}px`;                        
                y = containerRect.height - this._halfHeight;
            }
            else {
                this._picker.style.top = `${y}px`;
            }
            const s = 1 - (x + this._halfWidth) / containerRect.width;
            const l = 1 - (y + this._halfHeight) / containerRect.height;
            const {r, g, b} = hsl2rgb(0, 0, 1 - l);            
            this._picker.style.borderColor = 'rgb(' + [r,g,b].join(',') + ')';            

            let event = document.createEvent('Event');
            event.initEvent('change', false, false);
            event.detail = {h: this._options.hue, s, l};
            this.dispatchEvent(event);
        }        
    }
    _stop (e) {
        e.stopPropagation();
        document.body.style.cursor = 'default';
        this._offsetX = 0;
        this._offsetY = 0;
        this._moving = false;
    }
    setHue (h) {
        this._options.hue = h;
        let ctx = this._canvas.getContext('2d');        
        let imgData = ctx.getImageData (0, 0, this._canvas.width, this._canvas.height);
        const {width, height} = imgData;        
        const alpha = this._options.alpha;        
        let k = 0;
        let data = new Uint8ClampedArray(width * height * 4);
        for (let i = height - 1; i >= 0; --i) {
            for (let j = width - 1; j >= 0; --j) {
                let s = j / width;
                let l = i / height;
                let {r, g, b} = hsl2rgb(h, s, l);
                data[k + 0] = r;
                data[k + 1] = g;
                data[k + 2] = b;
                data[k + 3] = Math.round (alpha * 255);
                k += 4;
            }
        }
        imgData.data.set(data);
        ctx.putImageData (imgData, 0, 0);        

        let event = document.createEvent('Event');
        event.initEvent('change', false, false);
        event.detail = this.hsl;
        this.dispatchEvent(event);
    }
    _process (e) {
        let ctx = this._canvas.getContext('2d');        
        let imgData = ctx.getImageData (0, 0, this._canvas.width, this._canvas.height);
        imgData.data.set(e.data);
        ctx.putImageData (imgData, 0, 0);

        let event = document.createEvent('Event');
        event.initEvent('change', false, false);
        event.detail = this.hsl;
        this.dispatchEvent(event);
    }    
    setAlpha (alpha) {
        this._options.alpha = alpha;
        this.setHue(this._options.hue);
    }

    get hsl () {
        const pickerRect = this._picker.getBoundingClientRect();
        const containerRect = this._container.getBoundingClientRect();
        const s = 1 - (pickerRect.left + this._halfWidth - containerRect.left) / containerRect.width;
        const l = 1 - (pickerRect.top + this._halfHeight - containerRect.top) / containerRect.height;
        return {h: this._options.hue, s, l};
    }
    set hsl ({h, s, l}) {
        const containerRect = this._container.getBoundingClientRect();
        this._picker.style.left = `${(1 - s) * containerRect.width - this._halfWidth}px`;    
        this._picker.style.top = `${(1 - l) * containerRect.height - this._halfHeight}px`;
        this.setHue (h);
    }
}

export default ColorArea;